:root
{
  --text-primary-color: #030303;
  --text-secondary-color: #888;
  --bg-primary-color: #fff;
  --bg-primary-hover-color: #9d9ea1;
  --bg-secondary-color: #f2f2f2;
  --bg-secondary-hover-color: #e6e6e6;
  --separation-line-color: #e5e5e5;
  --yt-brand-color: #f00;
  --suggested-action-color: #378de9;
  --call-to-action-color: #3ea6ff;
}

@media (prefers-color-scheme: dark) 
{
  :root
  {
    --text-primary-color: #fff;
    --text-secondary-color: #888;
    --bg-primary-color: #0f0f0f;
    --bg-primary-hover-color: #9d9ea1;
    --bg-secondary-color: #272727;
    --bg-secondary-hover-color: #3d3d3d;
    --separation-line-color: #3f3f3f;
    --suggested-action-color: #1d5fd4;
    --call-to-action-color: #3978e6;
  }
}

:where(
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  table,
  tr,
  td,
  th,
  p,
  li,
  span
){
  color: var(--text-primary-color);
}

body {
	font-family: 'Source Sans Pro', 'Roboto', 'Noto', 'Arial', sans-serif;
	background: var(--bg-primary-color);
	color: var(--text-primary-color);
  scrollbar-gutter: stable;
}
.modal-content {
	background: var(--bg-primary-color);
	color: var(--text-primary-color);
}
tr:not(:first-child) {
	background-color: var(--bg-secondary-color);
	transition: all 0.3s ease;
}
tr:not(:first-child):hover {
	background-color: var(--bg-secondary-hover-color);
}
#keybind-input {
	background-color: var(--bg-primary-hover-color);
	caret-color: var(--text-primary-color);
	color: var(--text-primary-color);
}

// Separator
.separation-line {
	background-color: var(--separation-line-color);
  margin-top: 5px;
  opacity: 0.9;
  border: none;
  outline: none;
}

.popup_subheading
{
  text-align: center;
  margin: 5px 0px
}

svg {
	fill: var(--bg-primary-hover-color);
	transition: all 0.5s ease;
}

svg:hover {
	cursor: pointer;
	fill: var(--yt-brand-color) !important;
}

.container {
	width: 280px;
}

.title-container {
	display: flex;
	justify-content: space-between;
	flex-direction: row;
}

.title {
	font-size: 16px;
	font-weight: bold;
}

.version {
	color: var(--bg-primary-hover-color);
}

.separation-line {
	height: 1px;
	width: 100%;
}

.textbox {
	width: 100%;
	font-size: 12px;
	margin: 0;
}

label {
	font-size: 12px;
}

#extra_options_skip_threshold {
	margin-left: 4px;
}

.textbox:focus {
	outline: 0;
	border-color: var(--call-to-action-color);
}

.keybind-wrapper {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	gap: 10px;
}

.keybind-span {
	padding: 3px 7px;
	background-color: var(--bg-secondary-hover-color);
	border-radius: 3px;
	box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.275);
	cursor: default;
}

.edit-svg {
	fill: var(--bg-primary-hover-color);
}

.edit-btn {
  background: none;
  outline: none;
  border: none;
}

table {
	border-collapse: separate;
	border-spacing: 0 2px;
}

th {
	font-size: 12px;
	text-align: center;
}

td {
	font-size: 12px;
	padding: 7px 5px;
}

td:first-child {
	padding-left: 6px;
}

td:first-child,
th:first-child {
	border-bottom-left-radius: 5px;
	border-top-left-radius: 5px;
	cursor: default;
}

td:last-child,
th:last-child {
	border-bottom-right-radius: 5px;
	border-top-right-radius: 5px;
}

.footer {
	font-size: 10px;
	text-align: center;
	margin: 5px 0px;
}

a {
	color: var(--text-primary-color);
	text-decoration: none;
	display: flex;
}

.btn-wrapper {
	display: flex;
	justify-content: center;
	flex-direction: row;
	gap: 8px;
}

.modal {
	display: none;
	position: fixed;
	z-index: 999;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	overflow: auto;
	background-color: rgba(0, 0, 0, 0.4);
}

.modal-content {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	margin: auto;
	padding: 0 8px 8px 8px;
	width: 80%;
	border-radius: 5px;
	box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.7);
}

.modal-header {
	display: flex;
	justify-content: space-between;
	flex-direction: row;
	margin-bottom: 4px;
	margin-top: 2px;
	align-items: center;
}

.modal-title {
	font-weight: bold;
}

.close-btn {
	color: var(--bg-primary-hover-color);
	font-size: 20px;
	font-weight: bold;
	text-decoration: none;
	cursor: pointer;
}

.close-btn:hover,
.close-btn:focus {
	color: var(--yt-brand-color);
	text-decoration: none;
	cursor: pointer;
}

.input-wrapper {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	margin: 10px 0px;
	gap: 10px;
}

.prevent-selection {
	-webkit-user-select: none; /* Safari */
	-ms-user-select: none; /* IE 10 and IE 11 */
	user-select: none; /* Standard syntax */
}

#keybind-input {
	width: 30%;
	height: 1.5rem;
	text-align: center;
}

#keybind-input:focus {
	outline: 0;
	outline: none !important;
	box-shadow: 0 0 3px #00000020;
}

.extra_options--row
{
  display: grid;
  grid-template-columns: 5fr 1fr;
  text-wrap: balance;
  margin-bottom: 16px;
}

.extra_options--row:last-child()
{
  margin-bottom: 0;
}


.extra_options--row input[type="number"],
.extra_options--row input[type="text"]
{
  outline: none;
  border: 1px var( --bg-secondary-hover-color ) solid;
  background: var( --bg-secondary-hover-color );
  color: var(--text-primary-color);
  padding: 0 1rem;
  border-radius: 100vh;
  width: 5rem;
}
.extra_options--row input[type="number"]:focus,
.extra_options--row input[type="text"]:focus
{
  border: 2px var(--yt-brand-color) solid;
}


.extra_options--row input[type="checkbox"],
.extra_options--row input[type="radio"],
.extra_options--row input[type="range"]
{
  accent-color: var(--yt-brand-color);
}

.extra_options--row input[type="number"]::-webkit-outer-spin-button,
.extra_options--row input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
.extra_options--row input[type="number"] {
  -moz-appearance: textfield;
}

/* width */
::-webkit-scrollbar {
  width: 0.5rem;
  
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
  height: 4rem;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: var(--bg-secondary-color);
  height: 4rem;
  border-radius: 1rem;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--bg-secondary-hover-color);
}


.--page-indicator-container
{
  display: flex;
  gap: 1rem;
  width: fit-content;
  margin: 0 auto;

  button, input[type="submit"], input[type="reset"] {
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
  }

  .--page-indicator,
  .--page-indicator-active
  {
    height: 1rem;
    aspect-ratio: 1/1;
    border-radius: 5rem;
    cursor: pointer;
  }
  
  // todo  - change this to be an svg (fill + stroke)
  .--page-indicator
  {
    border: 2px solid var( --text-secondary-color );
    background: transparent;
  }
  .--page-indicator-active
  {
    border: 2px solid var( --text-primary-color );
    background: var( --text-primary-color );
  }
}

.--footer-button-container
{
  display: flex;
  width: fit-content;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  max-width: 100%;
  margin: 0 auto;

  button, input[type="submit"], input[type="reset"] {
    background: none;
    color: inherit;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
  }
  
  .--footer-button {
    margin: 5px 0px;
    padding: 5px 10px;

    background-color: transparent;
    color: var(--yt-brand-color );
    outline: 1px var( --yt-brand-color ) solid;

    border-radius: 3px;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.275);
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 12px;
  }
  
  .--footer-button:hover {
    background-color: var(--yt-brand-color );
    color: var(--text-primary-color);
  }
}

// this is for the github link
.--global-footer
{
  display: flex;
  width: fit-content;
  max-width: 100%;
  margin: 0 auto;

  .--global-footer-link
  {
    color: var(--text-secondary-color);
  }
}